import { css } from '@emotion/core'
import { theme } from 'assets/styles';

export const main = css`
  background: ${theme.colors.bg};
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: .8em;
  border-right: none;

  @media (min-width: ${theme.breakpoints.md}) {
    border-right: grey 2px solid;
  }
`;
